.App {
  text-align: center;
}

.coisasSobreMim {
  width: 90%;
  margin: 0 auto;
  font-family: 'Indie Flower'
}

.Wrapper {
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.botao{
  font-family: 'Lato';
}

.coisasSobreMim > p {
  margin-top: 20px;
  font-size: 19px;
  padding: 20px;
  border-bottom: 2px solid black;
  font-family: 'Indie Flower';
  -webkit-box-shadow: 2px 1px 6px #000;
          box-shadow: 2px 1px 6px #000;
}

.main {
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  padding-left: 2px;
  /* background-color: #bbbb99; */
}

.leftSide {
  /* background-color: #bbbbbb; */
  padding-left: 20px;
  padding-right: 20px;
  /* background-color: #000; */
}

.fixar {
  position: fixed;
}

@media screen an (max-width: 680px){
  div.App > button {
    margin-left: 0px;
  }
  .leftSide {
    padding-left: 20px;
    padding-right: 20px;
    /* background-color: #000; */
  }

}



