.Nota{
  margin-left: 0px;
  margin-bottom: 15px;
  text-align: left;
  display: block;
  padding: 30px;
  cursor: pointer;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  width: 100%;
}

.Nota:hover {
  background-color: #f55;
  color: white;
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  -webkit-box-shadow: 0 2px 3px #f77;
          box-shadow: 0 2px 3px #f77;
}

.Active {
  background-color: #b30000;
  color: white;
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
}

.Active:hover {
  background-color: #f55;
  -webkit-box-shadow: 0 2px 3px #f55;
          box-shadow: 0 2px 3px #f55;
}

.Active > h4.notaTitulo{
  color: white;
}

.Nota:active {
  background-color: #f99;
  color: white;
}

.Nota:hover > h4.notaTitulo {
  color: white;
}
.notaTitulo {
  color: #b30000;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

@media (max-width: 680px) {
  .Nota {
    margin-left: 0px;
  }
}



